.paginationBttns {
    margin-top: 50px;
    margin-bottom: 50px;
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    box-shadow: 2px 1px 7px rgba(0, 0, 0, 0.08),
      0px 2px 1px -1px rgba(0, 0, 0, 0.04), 0px 1px 3px rgba(0, 0, 0, 0.12);
    border-radius: 10.4px;
  }
  
  .paginationBttns a {
    margin-left: 30px;
    margin-right: 30px;
  
    font-family: ProximaNova-Bold;
    padding-left: 10px;
    padding-right: 10px;
    font-style: normal;
    font-weight: 700;
    font-size: 20.8px;
    line-height: 25px;
    letter-spacing: 1.73333px;
    color: #70778b;
  }
  
  .paginationActive a {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: relative;
    color: #5876c5;
  }
  .paginationActive a::after {
    margin-bottom: -23px;
    position: absolute;
    content: '';
    width: 100%;
    height: 4px;
    background: #5876c5;
  }